:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.is-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  pointer-events: none;
  margin: -1px;
  position: absolute;
}

.header {
  z-index: 99;
  min-height: 60px;
  height: 60px;
  width: 100%;
  color: #fff;
  background-color: #3f57b3;
  justify-content: center;
  align-items: center;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.header input {
  min-width: 300px;
}

.gallery {
  max-width: 90%;
  grid-gap: 20px 16px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 310px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  border-radius: 0 0 4px 4px;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.gallery__box {
  height: 70px;
}

.gallery__box > ul {
  min-width: 100%;
  height: 100%;
  flex-direction: row;
  justify-content: space-around;
  align-items: baseline;
  padding: 10px 0 0;
  list-style: none;
  display: flex;
}

.gallery-header {
  margin: 0 0 6px;
  padding: 0;
  font-size: 18px;
}

.gallery-text {
  margin: 0;
  padding: 0;
}

.gallery__box li {
  color: #000;
  text-align: center;
}

.gallery__box > p {
  margin: 0;
  padding: 10px;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 240px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.search-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-more {
  color: #eee;
  background-color: #3f57b3;
  margin-top: 40px;
  margin-bottom: 200px;
  padding: 6px 10px;
}

